.login-container {
  // background-image: url("https://cutewallpaper.org/23/blue-colour-wallpaper-hd/367314301.jpg");
  background-color: #f5f5f5;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100%;
  min-height: 768px;

  .Login {
    padding: 20vh;

    form {
      width: 400px;
      margin: auto;

      .container {
        border-radius: 12px;
        border: solid white 3px;
        height: fit-content;
        width: 400px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        /* align-items: center; */
        padding: 20px;
        background-color: white;
        box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.1),
          0 2px 6px 2px rgba(60, 64, 67, 0.15);

        .H1 {
          // background-color: white;

          font-size: 25px;
          font-weight: bold;
          color: #00ab9f;
          text-align: center;
          text-shadow: 2px 2px 5px #63ece3;
          padding: 10px;
          margin-top: 10px;
          letter-spacing: 2px;
          // border-bottom: 1px solid rgba(5, 5, 5, 0.2);
        }

        label {
          margin-top: 10px;
          font-weight: bold;
          margin-bottom: 10px;
        }

        input[type="text"],
        input[type="password"] {
          width: 100%;
          padding: 12px 20px;
          margin: 8px 0;
          display: inline-block;
          border: 1px solid #ccc;
          box-sizing: border-box;
        }

        button {
          background-color: #25b9af;
          color: rgb(250, 250, 250);
          padding: 14px 20px;
          margin: 8px 0;
          border: none;
          cursor: pointer;
          width: 100%;
          font-weight: 700;
          border-radius: 5px;
        }

        button:hover {
          opacity: 0.8;
        }

        input[type="checkbox"] {
          margin-top: 16px;
        }

        .psw {
          text-align: right;
          margin-top: 10px;
          margin-right: 5px;
        }

        .psw a {
          color: #25b9af;
          text-decoration: none;
        }
      }

      .error {
        color: red;
      }
    }
  }
}

