.user-order-wrapper {
  margin: 0 auto 20px;

  h1 {
    text-align: center;
    padding: 20px;
    font-size: 22px;
  }

  .cart-list {
    margin-bottom: 20px;

    .cart-item {
      display: flex;
      align-items: center;
      border: 1px solid #ccc;
      border-radius: 4px;
      padding: 10px;
      margin-bottom: 10px;

      .cart-item-image {
        flex: 0 0 80px;
        margin-right: 10px;

        img {
          width: 100%;
          height: auto;
          border-radius: 4px;
        }
      }

      .cart-item-info {
        flex: 1;

        .cart-item-name {
          font-size: 16px;
          margin: 5px 0;
        }

        .cart-item-price {
          font-size: 16px;
          color: #c92127;
          margin: 5px 0;
        }

        .cart-item-quantity {
          width: 40px;
          padding: 5px;
          border: 1px solid #ccc;
          border-radius: 4px;
        }

        .quantity-input {
          display: flex;
          align-items: center;

          input {
            width: 40px;
            padding: 5px;
            border: 1px solid #ccc;
            border-radius: 4px;
            text-align: center;
          }

          button {
            background-color: #817d7d;
            color: #fff;
            border: none;
            border-radius: 4px;
            padding: 5px 10px;
            cursor: pointer;
            margin: 0 5px;
          }
        }

        .quantity-remaining {
          color: #000000;
          font-style: italic;
          margin: 5px 0;
        }
      }

      .cart-item-actions {
        flex: 0 0 80px;

        .remove-button {
          background-color: #c92127;
          color: #fff;
          border: none;
          border-radius: 4px;
          padding: 5px 10px;
          cursor: pointer;
        }
      }
    }
  }

  .cart-total {
    display: flex;
    justify-content: right;
    padding: 0 10px;
    margin-bottom: 10px;

    .cart-total-label {
      font-weight: bold;
      margin-right: 5px;
    }

    .cart-total-amount {
      font-size: 18px;
      color: #c92127;
    }
  }

  .checkout-button {
    background-color: #817d7d;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    cursor: pointer;
    width: 100%;
  }
}

.order-noitem {
  background-color: rgb(255, 255, 255);
  width: 80%;
  margin: 30px auto;
  padding-bottom: 10px;
  border-radius: 5px;

  .img {
    width: fit-content;
    margin: 30px auto;

    img {
      margin-top: 30px;
      width: 200px;
    }
  }

  .message {
    width: fit-content;
    margin: 30px auto;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
  }

  .btn-shopping {
    margin: 30px auto;
    width: fit-content;

    button {
      border-style: none;
      background-color: rgb(41, 206, 184);
      color: rgb(255, 255, 255);
      width: 300px;
      font-size: 20px;
      font-weight: 500;
      padding: 5px 30px;
      border-radius: 20px;
    }

    button:hover {
      background-color: #1e6d68;
    }
  }
}

//Ipad
@media (max-width: 840px) {
  .order-noitem {
    width: 100%;
    margin: 0;
    min-width: 0;
    display: block;

    .img {
      margin: 0 auto;
    }
  }
}
